body {
    font-family: "Georgia", serif;
    font-size: 16px;
    line-height: 1.5;
    position: relative;
}
.App {
  text-align: center;
}

.logo div{
  animation: App-logo-shake infinite 2.5s linear;
  height: 30px;
    width: 220px;
    background: beige;
    text-align: center;
}



header {
  padding: auto 15px;
    position: fixed;
    z-index: 10;
    background: white;
    width: 100%;
    height: 66px;
    box-shadow: 0 1px 3px rgba(100,100,100,.7);
}
header ul.nav{
  list-style: none;
  padding-inline-start:0;
}

ul.nav a,ul.nav a:visited{
    color: #333333;
    font-size: 85%;
    text-transform: uppercase;
    font-family: sans-serif;
    font-weight: 500;
    line-height: 1.5;
    vertical-align: baseline;
}

header ul.nav li{
  display: inline-block;
  margin: auto .5rem;
}

.page{
    /* 往下便宜出导航的高度，避免与导航重叠 */
    padding-top: 66px;
}
.image-slider{
  position: relative;
  height: 600px;
    background-color: black;
}
.image-slider img{
  position: fixed;
  width: 100%;
  height: 600px;
  object-fit: cover;
    object-position: center;
  display: block;
  transition: all ease-in-out .5s;
  opacity: 1;
    z-index: 0;
}
.image-slider img.active{
  opacity: 1;
    /*display: block;*/
  border: 1px solid red;
    transition: opacity .3s;

    filter: blur(0);
    transition: filter .3s;
}


.page .body{
    z-index: 1;
    background: white;
    position: absolute;
    width: 100%;
}

.page .body .mainText{
    background-color: white;
    margin: -96px auto 0 auto;
    width: 80%;
    padding: 20px 40px;
    max-width: 860px;


}
.page .body .mainText h1{
    text-align: center;
    font-family: "Bodoni Italic", "Bodoni", "Georgia", serif;
    font-style: italic;
    font-size: 3.5rem;
    font-weight: normal;
    padding: 0;
    line-height: 1.1;
}
.page .body .mainText p:first-child{
    text-align: center;
    font-family: "Bodoni Italic", "Bodoni", "Georgia", serif;
    font-style: italic;
}


.page .body section .main{
    display: flex;
    flex-wrap: wrap;
    flex-flow: row;
    align-items:center;
    /*justify-content: center;*/
    background-size: cover;
    background-position: center;
    min-height: 120px;
    /*padding: 100px 80px;*/
}

.page .body section h2{
    text-align: center;
    font-family: "Bodoni Italic", "Bodoni", "Georgia", serif;
    font-style: italic;
    font-weight: normal;
    font-size: 2.6rem;
    margin: 50px auto;
}

.textCard{
    background-color: white;
    text-align: center;
    width: 380px;
    padding: 20px 30px 40px 30px;
    box-shadow: 0 0 5px rgba(155,155,155,.6);
}

.textCard h3{
    font-size: 2.8125rem;
    line-height: 1.1;
    margin-bottom: 20px;
    font-family: "Bodoni Italic", "Bodoni", "Georgia", serif;
    font-style: italic;
    font-weight: normal;
}

.bigImgCard{
    background-color: white;
    text-align: center;
    width: 580px;
    height: 500px;
    /*padding: 20px 30px 40px 30px;*/
    margin: 2rem;
    margin-top: 0;
    /*box-shadow: 0 0 5px rgba(155,155,155,.6);*/
    position: relative;
}
.bigImgCard .text{
    position: absolute;
    top: 240px;
    width: 100%;
}
.bigImgCard h3{
    font-family: "Bodoni", "Georgia", serif;
    font-size: 2rem;
    line-height: 4rem;
    height: 4rem;
    padding-top: 1rem;
    font-weight: normal;
    display: block;
    width: 380px;
    margin: 0px auto 20px auto;
    background-color: white;
    z-index: 10;
    /*box-shadow: 0 0 5px rgba(155,155,155,.6);*/
}
.bigImgCard .des{
    line-height: 1.5;
    height: 4.5rem;
    width: 460px;
    margin: 0 auto 2rem auto;
}

.smallImgCard{
    width:380px;
    margin: 1rem;
}
.smallImgCard .small{
    font-style: italic;
}

.smallImgCard .title{
    display: block;
    font-size: 1.7rem;
    line-height: 2rem;
    height: 6rem;
}
.smallImgCard:link, .smallImgCard:visited{
    color: black;
}
.smallImgCard:hover span{
    opacity: .8;
}


.textCard a:link,.textCard a:visited,
.bigImgCard a:link,.bigImgCard a:visited
{
    color: orange;
    text-transform: uppercase;
    display: block;
    margin: 15px auto;
}



.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
@keyframes App-logo-shake {
    from { opacity: 1; font-weight: bolder}
    to { opacity: .7;font-weight: normal}
}


.content{
  text-align: left;
}
article{
  /*background: #333;*/
}


.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 100ms ease-in;
}
.noFound{
    text-indent: 30px;
}


footer{
    width: 100%;
    height: 80px;
    position: absolute;
    z-index: 1;
    border-top:2px solid black;
    font-family: "Bodoni Italic", "Bodoni", "Georgia", serif;
    font-style: italic;
    font-size: 85%;
    line-height: 1.5;
    padding: 15px 0;
}
footer div{
    position: relative;
    margin: 15px 40px;
    display: flex;
    flex: 2;
}
footer div .right{
    text-align: right;
    display: block;

    position: absolute;

    right: 0;
}

a,a:link{
    text-decoration: none;
}
a.active,ul.nav a.active{
    color: orange;
}
a:hover,ul.nav a:hover,
.textCard a:hover,
.bigImgCard a:hover,
.smallImgCard:hover .title,
button{
    color: orangered;
}
